.blog_page_ui {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: var(--primary-color);
	width: 100vw;
	height: 100%;
}

.blog_post_container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 820px;
	height: 100%;
	margin-top: 90px;
	margin-bottom: 20px;
	font-family: var(--font-family-design);
}

.post_header {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 100%;
}

.post_title {
	font-size: 1.2em;
	font-weight: 500;
	margin: 10px 0;
	text-align: left;
} .post_subtitle {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 10px 0;
	height: 40px;
	font-size: 0.5em;
	color: var(--primary-color-light);
} .left_container {
	display: flex;
	flex-direction: row;
} 
.subtitle_info {
	margin: 0 20px;
	text-align: left;
	display: flex;
	flex-direction: column;
	align-self: flex-start;
} .subtitle_info p {
	font-weight: 500;
	height: 20px;
	margin: 0;
} .name {
	display: flex;
	align-items: center;
	text-transform: uppercase;
	font-size: 0.9em;
	color: var(--primary-color);
} .date {
	font-family: var(--font-family-text);
	display: flex;
	align-items: center;
	font-size: 0.9em;
}
.post_substack_link {
	border-radius: 10%;
	width: 40px;
	height: 40px;
	align-self: flex-start;
	object-fit: cover;
} .heart_button {
	align-items: flex-end;
}

.post_body {
	font-size: 0.7em;
	letter-spacing: 1px;
	font-weight: 300;
	line-height: 2; /*not really sure what unit this is in*/
	text-align: left;
	width: inherit;
}

.post_img {
	justify-content: center;
	width: 100%;
	height: 550px;
	margin: 20px 0;
	object-fit: cover;
}

.post_break {
	width: 100%;
	border-bottom: 1px solid var(--break-color);
}

.comment_section {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 820px;
	margin-top: 20px;
	margin-bottom: 40px;
	
}
.comment_box {
	width: 820px;
	border-bottom: 0px solid var(--break-color);
}
.comment {
	width: inherit;
	text-align: left;
	font-family: var(--font-family-text);
} .comment_data {
	color: var(--primary-color-light);
	font-size: 10px;
} .comment_content {
	font-size: 15px;
}
